<template>
  <v-container :fluid="true">
    <v-row>
      <v-col cols="12" md="4" offset-md="4" sm="4" offset-sm="4">
        <DefaultAdministrator @register="register" :users-list="users" />
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import DefaultAdministrator from "../../components/Forms/DefaultAdministrator";

export default {
  name: "CreateAdministrator",
  metaInfo: {
    title: "Шестерёнка | Создание администратора"
  },
  data() {
    return {};
  },
  computed: {
    ...mapGetters({
      users: "getUsers"
    })
  },
  methods: {
    ...mapActions({
      loadUsers: "fetchUsers",
      registerDefaultAdministrator: "fetchRegisterDefaultAdmin"
    }),
    async register(form) {
      await this.registerDefaultAdministrator(form);
      this.$toast("Администратор успешно создан!", {
        color: "success",
        x: "right",
        y: "top"
      });
    }
  },
  components: {
    DefaultAdministrator
  },
  async mounted() {
    await this.loadUsers();
  }
};
</script>

<style scoped></style>
