<template>
  <v-form v-model="valid">
    <v-container :fluid="true">
      <div class="mb-3">
        <h2 class="font-weight-bold">Создание администратора</h2>
      </div>
      <v-row>
        <v-col class="mb-2" cols="12">
          <v-select
            v-model="form.user"
            :items="usersList"
            :item-text="viewUserSelect"
            return-object
            label="Выберите пользователя, которого хотите назначить администратором"
          />
        </v-col>
        <v-col class="mb-2" cols="12">
          <v-text-field
            v-model="form.login"
            :rules="loginRules"
            label="Логин администратора"
            required
          />
        </v-col>
        <v-col class="mb-2" cols="12">
          <v-text-field
            v-model="form.password"
            :rules="loginRules"
            type="password"
            label="Пароль администратора"
            required
          />
        </v-col>
        <v-col class="mb-2">
          <v-btn @click="toggle" elevation="2" color="primary">
            Создать
          </v-btn>
        </v-col>
      </v-row>
    </v-container>
  </v-form>
</template>

<script>
export default {
  name: "DefaultAdministrator",
  data() {
    return {
      valid: false,
      form: {
        user: null,
        login: "",
        password: ""
      },
      userIdRules: [
        v =>
          !v ||
          "Выберите пользователя, которого хотите назначить администратором"
      ],
      loginRules: [
        v => !(v.length < 6) || "Длина логина должна быть не меньше 6 символов"
      ],
      passwordRules: [
        v => !(v.length <= 6) || "Длина пароля должна быть не меньше символов"
      ]
    };
  },
  methods: {
    toggle() {
      if (!this.valid) {
        return false;
      }

      this.$emit("register", this.form);
      this.$router.push({ name: "Login" });
    },
    viewUserSelect(user) {
      return `${user.id} - ${user.information.first_name}`;
    }
  },
  props: {
    usersList: {
      type: Array,
      required: true
    }
  }
};
</script>
